import React, { useEffect, useState } from 'react'
import { Avatar, Card, ListItem, Text } from 'react-native-elements'
import { getEtherBalances, getTokensBalances } from '@mycrypto/eth-scan'

import { CHAIN_BASETOKEN, TOKEN_MAP, getNodeURL } from 'helpers/nodes'

export const EthScanBalance = ({ chainId, term }) => {
  const [nativeToken, setNativeToken] = useState(0)
  const [tokens, setTokens] = useState({})

  useEffect(() => {
    const nodeUrl = getNodeURL(chainId)
    const addr = [term]

    async function fetchBalances() {
      const ethBalances = await getEtherBalances(nodeUrl, addr)
      Object.values(ethBalances).map((val, idx) => {
        if (val === 0n) setNativeToken(0)
        setNativeToken((Number(val) / 10 ** 18).toFixed(8))
      })
    }

    async function fetchTokensBalances() {
      const tokenMap = TOKEN_MAP[chainId]
      const tokenBalances = await getTokensBalances(
        nodeUrl,
        addr,
        tokenMap.map((entry) => entry.address)
      )
      const data = []
      Object.keys(tokenBalances).map((
        addr // multi addr
      ) =>
        Object.entries(tokenBalances[addr]).map(([key, val]) => {
          if (val === 0n) return

          const token = tokenMap.find((entry) => entry.address === key)
          data.push({
            // address: addr,
            token: token.symbol,
            balance: (Number(val) / 10 ** token.decimals).toFixed(4),
            source: token.name || '',
          })
        })
      )
      setTokens(data)
    }

    fetchBalances(chainId, term)
    fetchTokensBalances(chainId, term)
  }, [chainId, term])

  return (
    <Card>
      <Card.Title>💰 Balance</Card.Title>
      <Card.Divider />
      <ListItem key='eth'>
        <Text>
          {CHAIN_BASETOKEN[chainId].padEnd(20, ' ​')} {nativeToken}
        </Text>
      </ListItem>
      {tokens.length > 0 &&
        tokens.map((token) => (
          <ListItem key={token.token}>
            <Text>
              {token.token.padEnd(20, ' ​')}
              {token.balance}
            </Text>
          </ListItem>
        ))}
    </Card>
  )
}

export default EthScanBalance
