import React, { useEffect, useState } from 'react'
import { Avatar, Card, ListItem, Text } from 'react-native-elements'
import { CHAIN_ETHEREUM, GAS_ESTIMATOR } from 'helpers/constants'
import _upperCase from 'lodash/upperCase'

export const GasFee = ({ chainId }) => {
  const [gasStat, setGasStat] = useState({})
  useEffect(() => {
    if (chainId !== CHAIN_ETHEREUM) return
    const estimator = GAS_ESTIMATOR.GASNOW
    fetch(estimator.api)
      .then((response) => response.json())
      .then((json) => {
        const data = estimator.processGasData(json)
        setGasStat(data)
      })
  }, [chainId])

  if (chainId !== CHAIN_ETHEREUM) return null

  return (
    <Card>
      <Card.Title>
        ⛽️ Gas Fee (via {_upperCase(gasStat.source) || ''})
      </Card.Title>
      <Card.Divider />
      <ListItem>
        {/* <Text style={{ fontSize: 32 }}>
          🚀{gasStat.H} 🚘{gasStat.M} 🚲{gasStat.L}
        </Text> */}
        <Text style={{ fontSize: 48 }}> 🚘 {gasStat.M}</Text>
      </ListItem>
    </Card>
  )
}

export default GasFee
