import React from 'react'
import { StyleSheet, View } from 'react-native'
import { H1, Header, Main, P, Footer } from '@expo/html-elements'

import { SearchBar } from 'components/SearchBar'
import { Explorers } from 'components/Explorers'
import { GasFee } from 'components/GasFee'
import { Balance } from 'components/Balance'
import { SupportUs } from 'components/SupportUs'
import useLocalStorage from 'hooks/useLocalStorage'
import { SUPPORT_CHAINS } from 'helpers/constants'
import { i18n } from 'helpers/i18n'

const bgColor = 'white'
const styles = StyleSheet.create({
  container: {
    alignItems: 'center',
    backgroundColor: bgColor,
    flex: 1,
    justifyContent: 'center',
  },
})

export default function Root() {
  const [term, onChangeSearchTerm] = useLocalStorage('addr', '')
  const [chainIdx, onChangeChainIdx] = useLocalStorage('chainId', 0)
  const chainId = SUPPORT_CHAINS[chainIdx]
  const onSearch = () => {
    console.log(term, chainId)
  }

  return (
    <View style={styles.container}>
      <Header>
        <H1>{i18n.en.title}</H1>
      </Header>
      <Main>
        <SearchBar
          chainIdx={chainIdx}
          onChangeChainIdx={onChangeChainIdx}
          term={term}
          onChangeSearchTerm={onChangeSearchTerm}
          onSearch={onSearch}
        />
        <GasFee chainId={chainId} />
        <Balance chainId={chainId} term={term} />
        <Explorers chainId={chainId} term={term} />
        <SupportUs />
      </Main>
      <Footer>
        <P>{i18n.en.credit}</P>
      </Footer>
      {/* <StatusBar style="auto" /> */}
    </View>
  )
}
